
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  .StartDialogue {
    display: flex;
    flex-direction: column;

    img {
      filter: none !important;
    }

    &__title {
      //color: #6a6b8e;
      color: rgba(255, 255, 255, 0.7);
      font-size: 24px;
      border-radius: 0.75em;
      text-align: center;
    }

    &__enemy {
      color: #adecd8;
      text-transform: uppercase;
      text-shadow: 2px 2px 6px #adecd8;
      font-size: 18px;
      overflow-wrap: break-word;
      background: #1b1b24;
      padding: 0.5em;
      border-radius: 0.75em;
      text-align: center;
      vertical-align: middle;
      display: inline-block;
      width: 70%;
      margin-left: 10px;

      &.StartDialogue__enemy--long {
        font-size: 16px;
      }
    }

    &__bottom {
      display: flex;
      align-items: center;
      color: #6a6b8e;
      margin: 16px auto 0;
      width: 100%;
      justify-content: space-evenly;
    }

    &__icon {
      max-height: 50px;
      max-width: 50px;
      align-items: center;
      margin: 0.5em 0;
      display: inline-flex;
      vertical-align: middle;

      img {
        max-width: 100%;
        max-height: 100%;
      }
    }

    &__countdown {
      margin: 0.5rem auto;
      font-size: 16px;
    }

    &__skill-selector {
      margin: 8px auto 16px !important;
      max-width: 85%;
      border: 2px solid #40bea2;
      border-radius: 16px;
      font-size: 16px;

      select {
        border: none !important;
        box-shadow: none !important;
        width: 100% !important;
        color: rgb(9, 250, 193) !important;
        background-color: transparent !important;
        cursor: pointer !important;
        padding: 8px !important;

        &:after {
          color: rgb(9, 250, 193) !important;
        }

        option {
          color: rgb(9, 250, 193) !important;
        }

        &:hover,
        &:focus {
          border: none;
        }
      }
    }

    &__skills {
      cursor: pointer;

      img {
        max-height: 30px;
        display: inline-block;
        box-shadow: 0 0 4px 2px rgb(9, 250, 193);
        border-radius: 8px;
      }

      .empty-skill {
        min-height: 30px !important;
        min-width: 30px !important;
        display: inline-block !important;
        border-radius: 8px !important;
        box-shadow: #fff 0 -1px 1px, #ff0 0 -1px 3px, #ff8000 0 -3px 5px, red 0 -9px 20px,
          3px 3px 10px 3px rgba(0, 0, 0, 0) !important;
        position: relative !important;
        vertical-align: top !important;

        img {
          padding-top: 4px !important;
          box-shadow: none !important;
        }

        ::v-deep .icon i.mdi:before {
          font-size: 24px;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          top: 0;
          height: 30px;
        }
      }
    }
  }
}
