
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  box-sizing: border-box!important;

  * {
    box-sizing: border-box!important;
  }

  .audio-control {
    height: 25px;
    width: 25px;
    display: inline-block;
    cursor: pointer;
    z-index: 2147483647;
    position: relative;
    background: transparent !important;
    margin-bottom: 3px;
    transform: scale(0.9);

    span {
      display: block;
      width: 8px;
      height: 8px;
      background: white;
      margin: 6px 0 0 -22px;

      &:after {
        content: "";
        position: absolute;
        width: 0;
        height: 0;
        border-style: solid;
        border-color: transparent white transparent transparent;
        border-width: 10px 14px 10px 15px;
        left: 0;
        top: 6px;
        box-sizing: content-box;
      }

      &:before {
        transform: rotate(45deg);
        border-radius: 0 50px 0 0;
        content: "";
        position: absolute;
        width: 5px;
        height: 5px;
        border-style: double;
        border-color: white;
        border-width: 7px 7px 0 0;
        left: 35px;
        top: 9px;
        transition: all 0.2s ease-out;
        box-sizing: content-box;
      }
    }

    &:hover {
      span:before {
        transform: scale(0.8) rotate(42deg);
      }
    }

    &.mute {
      span:before {
        left: 5px;
        transform: scale(0.5) rotate(36deg);
        opacity: 0;
      }
    }

    &.changing {
      color: rgb(177, 162, 98);
    }
  }
}

@media (max-width: 1200px) {
  #zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
    .audio-control {
      display: none;
    }
  }
}
