
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  .HealthChange {
    font-family: Impact, sans-serif!important;
    display: flex;
    align-items: center;
    padding: 0.25em 1em;
    position: fixed;
    bottom: 42%;
    left: 9em;
    background: none;
    color: lightgreen;
    font-weight: 800;
    font-size: 1.6em;
    z-index: 2147483647;
    border-radius: 5px;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #000;
    text-shadow: 1px 1px 0 black, 2px 2px 0 black;
    letter-spacing: 1px;
    /*transition: opacity 2s linear;*/
    animation: moveUp 2.5s ease-in-out forwards;

    &--negative {
      /*color: #f1f1f1;*/
      color: #ef9437;
    }

    &--enemy {
      left: auto;
      right: 9em;
    }
  }
}

@keyframes moveUp {
  0% {
    transform: translateY(80px);
    opacity: 0.4;
  }
  50% {
    transform: translateY(40px);
    opacity: 1;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

@media (max-width: 1000px) {
  #zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
    .HealthChange {
      left: 15vw;

      &--enemy {
        left: auto;
        right: 15vw;
      }
    }
  }
}
