
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  font-size: 16px;

  .HealthBar {
    display: flex;
    align-items: center;
    background: #404066;
    border-radius: 0.75em;
    padding: 3px 8px;

    &--healthy {
      border: 2px solid #60e2da;
    }

    &--dead {
      border: 2px solid red;
    }

    &__capsule {
      padding: 0.1em 0.2em;
      background: #373955;
      border-radius: 10px;
      width: 100%;
    }

    &__bar {
      position: relative;
      background: #2d2e4d;
      border-radius: 10px;
      height: 12px;
      border: 2px solid #383954;

      &__currentHealth {
        border-radius: 10px;
        background: #42ba86;
        height: 100%;
        transition: width 0.3s ease-out;

        &--shield {
          position: absolute;
          top: 0;
          right: 0;
          background: #ececec;
          border-radius: 0 10px 10px 0;
        }
      }

    }

    &__stats {
      display: flex;
      margin-left: 0.5em;
      color: #9495be;
      white-space: nowrap;
      font-size: 14px;

      &__separator {
        padding: 0 0.3em;
      }
    }

    &__tooltip {
      position: relative;
      display: inline-block;
      border-bottom: 1px dotted black;

      &:hover .HealthBar__tooltip-text:not(.HealthBar__tooltip-text--hidden) {
        visibility: visible;
      }
    }

    &__tooltip-text {
      visibility: hidden;
      width: 120px;
      background-color: black;
      color: #fff;
      text-align: center;
      padding: 5px 0;
      border-radius: 6px;
      position: absolute;
      z-index: 1;
      bottom: 100%;
      left: 50%;
      margin-left: -60px;
    }
  }
}

@media (max-width: 800px) {
  #zd5cc103-5d5b-4c3e-aec7-a15805dba881 .HealthBar {
    flex-direction: column;
  }
}
