
p {
  font-size: 20px;
}

#dedalium-game-control {
  box-sizing: border-box !important;
  border: none !important;
  outline: 0 !important;
  line-height: 14px !important;
  border-radius: 5% !important;
  font-size: 24px !important;
  width: 118px !important;
  height: 40px !important;
  padding: 3px 3px 3px 30px !important;
  min-width: auto !important;
  max-width: none !important;
  min-height: auto !important;
  max-height: none !important;
  position: fixed !important;
  letter-spacing: normal !important;
  cursor: pointer !important;
  opacity: 1 !important;
  z-index: 2147483647 !important;
  display: block !important;
  background: #40bea2 !important;
  color: white !important;
  transform: none !important;
  box-shadow: 2px 4px 12px 0 #40bea2 !important;
  font-family: sans-serif !important;
  font-weight: 800 !important;
  bottom: 120px;
  left: -121px;

  img {
    filter: none!important;
  }

  .toggle-wrapper {
    padding: 0!important;
    outline: none!important;
    height: 100% important;
    display: block;
    width: 90px;
  }

  .toggle-wrapper > span {
    border: none !important;
    outline: 0 !important;
    text-align: center !important;
    vertical-align: middle !important;
    height: 100% !important;
    -webkit-text-size-adjust: 100% !important;
    word-wrap: break-word !important;
    font: inherit !important;
    text-transform: none !important;
    line-height: 20px !important;
    font-size: 32px !important;
    letter-spacing: normal !important;
    cursor: pointer !important;
    color: white !important;
    font-family: sans-serif !important;
    font-weight: 800 !important;
    box-sizing: border-box !important;
    padding: 0 !important;
    width: 42%!important;

    .dedalium-game-control-image {
      padding: 5px!important;
      height: 100%!important;
      text-align: center !important;
      vertical-align: middle !important;
      width: 45% !important;
      display: inline-block !important;
      background: black !important;
      border-radius: 7px !important;
      height: 100% !important;
      -webkit-text-size-adjust: 100% !important;
      word-wrap: break-word !important;
      font: inherit !important;
      text-transform: none !important;
      line-height: 20px !important;
      font-size: 32px !important;
      letter-spacing: normal !important;
      cursor: pointer !important;
      color: white !important;
      font-family: sans-serif !important;
      font-weight: 800 !important;
      box-sizing: border-box;

      img {
        border: none !important;
        outline: 0 !important;
        display: inline-block !important;
        vertical-align: middle !important;
        transform: none !important;
        max-width: 26px !important;
        margin-top: -2px;
        padding: 0 !important;
        -webkit-text-size-adjust: 100% !important;
        word-wrap: break-word !important;
        font: inherit !important;
        text-transform: none !important;
        line-height: 20px !important;
        font-size: 24px !important;
        letter-spacing: normal !important;
        cursor: pointer !important;
        color: white !important;
        font-family: sans-serif !important;
        font-weight: 800 !important;
        box-sizing: border-box !important;
        max-height: 25px;
      }
    }

    .dedalium-game-control-text {
      padding: 0 !important;
      vertical-align: middle !important;
      display: inline-block !important;
      transform: none !important;
      -webkit-text-size-adjust: 100% !important;
      word-wrap: break-word !important;
      font: inherit !important;
      text-transform: none !important;
      line-height: 20px !important;
      letter-spacing: normal !important;
      cursor: pointer !important;
      color: white !important;
      font-family: sans-serif !important;
      font-weight: 800 !important;
      box-sizing: border-box !important;
      font-size: 14px!important;
      text-align: right!important;
      width: 44%!important;
    }
  }

  .config-wrapper {
    display: inline-block !important;
    padding: 5px !important;
    text-align: left !important;
    position: absolute !important;
    top: 50% !important;
    left: 0 !important;
    background: 0 !important;
    transform: translateY(-50%) !important;
    border-right: 1px solid white !important;

    &:hover > img {
      filter: invert(100%) sepia(11%) saturate(1%) hue-rotate(8deg) brightness(105%) contrast(101%) !important;
    }

    img.config-image {
      max-height: 20px;
      max-width: 20px;
    }
  }

  &.dedalium-game-control--paused {
    background: #b40000 !important;
    box-shadow: 2px 4px 12px 0 #b40000 !important;

    .dedalium-game-control-image img {
      max-height: 20px!important;
      max-width: 20px!important;
    }
  }
}

#dedalium-game-control-wrapper {
  width: 15px !important;
  height: 60px !important;
  min-width: auto !important;
  max-width: none !important;
  min-height: auto !important;
  max-height: none !important;
  position: fixed !important;
  cursor: pointer !important;
  z-index: 2147483647 !important;
  display: block !important;
  background: transparent !important;
  bottom: 120px;
  left: 0;

  &:hover {
    #dedalium-game-control {
      left: 2px;
      min-width: 128px!important;
      animation: linear;
      animation-name: slide-right;
      animation-duration: 0.1s;
    }
  }
}

body.dedalium--blocked {
  #appNetworkGuardian,
  #dedalium-game-control-wrapper {
    display: none;
  }
}

@-webkit-keyframes slide-right {
  0% {
    left: -110px;
  }
  25% {
    left: -85px;
  }
  50% {
    left: -60px;
  }
  75% {
    left: -35px;
  }
  100% {
    left: -5px;
  }
}
