
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  .BattleResult {
    display: flex;
    flex-direction: column;
    transform: skew(2deg);

    &__title {
      color: #6a6b8e;
      font-size: 20px;
      border-radius: 0.75em;
      text-align: center;
      padding: 0.5em;
    }

    &__winner {
      margin-top: 0.2em;
      color: #adecd8;
      text-transform: uppercase;
      text-shadow: 2px 2px 6px #adecd8;
      font-size: 16px;
      background: #1b1b24;
      padding: 0.5em;
      border-radius: 0.75em;
      text-align: center;
    }

    &__bottom {
      display: flex;
      flex-direction: column;
      align-items: center;
      color: #6a6b8e;
      margin-top: 1em;
    }

    &__exp {
      position: absolute;
      left: 8px;
      color: rgba(255, 255, 255, 0.5);
      font-size: 12px;
    }

    &__stats {
      display: flex;
      align-items: center;
      color: #5c5e85;
      font-weight: bold;
      font-size: 1.1em;
      background: #1b1b24;
      padding: 0.5em;
      border-radius: 0.75em;
      flex: 1;
      margin-right: 1.5em;

      > div {
        /*margin-left: 2em;*/
        margin: 0 0.5em;
      }
    }

    &__message {
      text-align: center;
      color: white;
      font-weight: 700;
      font-size: 14px;
      padding: 0.5em;
      border-radius: 0.75em;
      font-family: 'Space Mono', monospace !important;
      -webkit-font-smoothing: antialiased;
    }
  }

  .gear-upgrade-section {
    width: 100%;
    display: flex;
    justify-content: center;
    cursor: pointer;
    margin-bottom: 8px;

    > .column.is-one-fifth {
      padding: 0;

      &:hover {
        img,
        button {
          transform: scale(1.1);
        }
      }
    }

    .next-level {
      margin-bottom: 4px;
      font-size: 10px;
    }

    .progress-wrapper {
      margin: 0 auto !important;
      max-width: 100%;

      p {
        font-size: 8px;
      }
    }

    button {
      overflow: hidden;
      cursor: pointer;
      background: linear-gradient(360deg, #40bea2 0%, #00795e 144.44%);
      box-shadow: 0px 8.0916px 8.0916px rgba(0, 0, 0, 0.25),
        0px 8.0916px 40.458px rgba(144, 235, 215, 0.7);
      color: white;
      padding: 0 8px;
      margin: 0 auto;
      font-size: 12px;
      font-weight: 700;
      z-index: 2;
      border: 3px solid;
      border-image-slice: 1;
      border-image-source: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.8) 0%,
        rgba(255, 255, 255, 0) 100%
      );
      min-width: auto;

      &:hover {
        background: linear-gradient(360deg, #00795e 0%, #40bea2 144.44%);
        box-shadow: 0 10px 10px rgba(0, 0, 0, 0.25), 0 10px 50px rgba(144, 235, 215, 0.7);
        border: 3px solid;
        border-image-slice: 1;
        border-image-source: linear-gradient(
          180deg,
          rgba(255, 255, 255, 0) 0%,
          rgba(255, 255, 255, 0.8) 100%
        );

        &::before {
          animation: shine 0.75s;
        }
      }

      &::before {
        position: absolute;
        top: 0;
        left: -75%;
        z-index: 2;
        display: block;
        content: '';
        width: 50%;
        height: 100%;
        background: -webkit-linear-gradient(
          left,
          rgba(255, 255, 255, 0) 0%,
          rgba(255, 255, 255, 0.3) 100%
        );
        background: linear-gradient(
          to right,
          rgba(255, 255, 255, 0) 0%,
          rgba(255, 255, 255, 0.3) 100%
        );
      }

      &.special-action {
        position: relative;
        vertical-align: top;
        margin: 0 3px 8px;
        padding: 3px;
        background: linear-gradient(360deg, #fac83f 0%, #a87e01 144.44%);
        color: white;
        border-image-source: linear-gradient(
          180deg,
          rgba(250, 200, 63, 1) 0%,
          rgba(168, 126, 1, 0) 100%
        );

        &:hover {
          background: linear-gradient(360deg, #a87e01 0%, #fac83f 144.44%);
        }
      }
    }

    img {
      max-height: 50px;
      max-width: 50px;
      margin: 0 auto;
      display: block;
    }
  }
}
