
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  .AttackInfo {
    font-family: Impact, sans-serif!important;
    display: flex;
    align-items: center;
    padding: 0.25em 1em;
    position: fixed;
    bottom: 45%;
    left: 9em;
    background: none;
    color: lightgreen;
    font-weight: 800;
    font-size: 1.8em;
    z-index: 9999;
    border-radius: 5px;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #000;
    text-shadow: 1px 1px 0 black, 2px 2px 0 black;
    letter-spacing: 1px;
    /*transition: opacity 2s linear;*/
    animation: moveUp 2.5s ease-in-out forwards;

    &--negative {
      animation: moveDown 2.5s ease-in-out forwards;
      color: #ef9437;
    }

    &--enemy {
      left: auto;
      right: 9em;
    }

    &--critical {
      color: red;
      animation: growUp 2.5s ease-in-out forwards;
    }
  }
}

@keyframes moveUp {
  0% {
    transform: translateY(80px);
    opacity: 0.6;
  }
  50% {
    transform: translateY(40px);
    opacity: 0.8;
  }
  75% {
    transform: translateY(20px);
    opacity: 1;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}


@keyframes moveDown {
  0% {
    transform: translateY(0px);
    opacity: 0.6;
  }
  50% {
    transform: translateY(40px);
    opacity: 0.8;
  }
  75% {
    transform: translateY(60px);
    opacity: 1;
  }
  100% {
    transform: translateY(80px);
    opacity: 1;
  }
}

@keyframes growUp {
  0% {
    transform: translateY(80px) scale(1);
    opacity: 1;
  }
  50% {
    transform: translateY(40px) scale(1.4);
    opacity: 0.8;
  }
  75% {
    transform: translateY(20px) scale(1.7);
    opacity: .6
  }
  100% {
    transform: translateY(0px) scale(2);
    opacity: 0;
  }
}

@media (max-width: 1000px) {
  #zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
    .AttackInfo {
      left: 15vw;

      &--enemy {
        left: auto;
        right: 15vw;
      }
    }
  }
}
