
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  .Border {
    border-radius: 24px;
    border: 2px solid #231f2b;
    background: #b1a262;
    padding: 0.4em;
    position: relative;

    &--bottomless {
      border-bottom: 0;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      padding-bottom: 0;

      .Border__inner {
        @extend .Border--bottomless;
      }
    }

    &--topless {
      border-top: 0;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      padding-top: 0;

      .Border__inner {
        @extend .Border--topless;
      }
    }

    &__inner {
      border-radius: 18px;
      background: #231f2b;
      padding: 2px;
      border: 4px solid #6a6f4a;
    }
  }
}
