
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  font-size: 16px;

  .Shell {
    $transition: all 0.2s ease-in;
    $barHeight: 25px;
    $maxShellWidth: 360px;
    $bottomMainShellSize: 42%;
    $bottomSecondaryShellSize: calc((100% - #{$bottomMainShellSize}) / 2);

    flex-basis: 40%; //$bottomSecondaryShellSize;
    max-width: $maxShellWidth;
    transition: $transition;
    z-index: 2147483646;

    * {
      font-family: sans-serif !important;
    }

    img {
      filter: none;
    }

    &--folded {
      .Shell__fold img {
        transform: rotate(180deg);
      }

      transform: translateY(calc(100% - #{$barHeight}));
    }

    &--bottomless {
      border-bottom: 0;

      .Shell__content {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        border-bottom: 0;
      }
    }

    &--topless {
      border-top: 0;

      .Shell__content {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        border-top: 0;
        font-size: 40px;
      }
    }

    &--light {
      .Shell__content {
        border: 5px solid #484873;
        background: #333453;
      }
    }

    &--invisible {
      visibility: hidden;
    }

    &--main {
      $marginX: 1em;
      flex-basis: calc(#{$bottomMainShellSize} - #{$marginX * 4});
      margin: 0 $marginX;
      left: 50%;
      position: fixed;
      bottom: -2px;
      transform: translateX(-50%);
      width: 400px;
      max-width: 40%;
      padding: 0!important;

      &.Shell--folded {
        transform: translateY(calc(100% - 25px)) translateX(-50%);
      }

      .Shell-Message__Username {
        display: inline-block;
        text-transform: uppercase;
        color: lightgreen;

        &.Shell-Message__Username--enemy {
          color: red;
        }

        &.Shell-Message__Username--long {
          font-size: 14px;
        }
      }

      .Shell-Message__Icon {
        max-height: 65px;
        border: 2px solid #6a6f4a;
        display: inline-block;
        max-width: 16%;
        vertical-align: middle;
      }

      .Shell-Message__Wrapper {
        vertical-align: middle;
        margin: 0 auto 0.5em;
        text-align: center;
        max-width: 95%;
      }

      .Shell-Message__Username-wrapper {
        display: inline-block;
        max-width: 78%;
        vertical-align: middle;
        text-align: center;
      }

      .Shell-Message__enraged {
        display: inline-block;
        text-transform: uppercase;
        font-size: 1.6em;
        color: red;
      }

      .Shell-Message__hint {
        margin-top: 1em;
        font-size: 1.6em;
      }

      .Shell-Message__Turn {
        display: block;
        font-family: sans-serif!important;
        color: rgb(177, 162, 98);
        margin-bottom: 0.5em;
        font-size: 18px;
      }

      .Shell-Message__close {
        position: absolute;
        right: 20px;
        top: 20px;
        width: 24px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        opacity: 0.8;
        z-index: 2;

        &:hover {
          opacity: 1;
        }
      }

      .Shell--main__exp {
        font-size: .9rem;
        position: absolute;
        left: 24px;
        top: 18px;
        color: rgba(255, 255, 255, 0.5);
      }
    }

    &__bar {
      display: flex;
      justify-content: space-between;
      padding: 0 2em;
      margin-bottom: -1px;

      > div {
        $background: rgba(0, 0, 0, 0.7);

        // https://stackoverflow.com/questions/36156604/webkit-clip-path-mysterious-line-in-chrome-when-scrollbar-present
        // The +1px solves the mistery line bug
        $clipPathSize: calc(100% + 1px);

        display: flex;
        align-items: center;
        height: $barHeight;
        color: white;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        width: 70px;
        clip-path: polygon(20% 0%, 80% 0%, 100% 100%, 0% 100%);

        div {
          background: $background;
          flex: 1;
          height: $barHeight;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 0 0.5em;
          font-weight: bold;
          font-size: 14px;

          &.Shell__title-text--long {
            font-size: 10px;
          }

          &.Shell__title-text--extra-long {
            font-size: 8px;
          }
        }
      }

      > div:not(:first-child) {
        margin-left: 1em;
      }
    }

    &__title {
      flex: 1;

      > div {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      &.Shell__title--gear {
        padding-top: 15px;
        font-size: 14px;
        max-width: 82%;
        margin: 0 auto;

        > div {
          margin-top: 15px;
          text-shadow: none;
          font-size: 16px;
        }

        .Shell__title-extra--trash {
          color: #a6a6a6;
        }

        .Shell__title-extra--trash {
          color: #a6a6a6;
        }

        .Shell__title-extra--common {
          color: #36bb4c;
        }

        .Shell__title-extra--rare {
          color: #7ed0ef;
        }

        .Shell__title-extra--rareplus {
          color: #2385f8;
        }

        .Shell__title-extra--epic {
          color: #9304d9;
        }

        .Shell__title-extra--legendary {
          color: #ff8407;
        }

        .Shell__title-extra--error {
          color: red;
        }
      }
    }

    &__fold {
      flex-grow: 0;
      transition: $transition;
      cursor: pointer;

      &:hover {
        opacity: 0.8;
      }

      img {
        transition: $transition;
      }
    }

    .Shell__audio {
      flex-grow: 0;
      cursor: pointer;

      &:hover {
        opacity: 0.8;
      }
    }

    &__content {
      text-align: center;
      border-radius: 12px 12px 0 0;
      border: 5px solid #343555;
      background: #1c1d25;
      color: white;
      font-weight: 700;
      font-size: 16px;
      padding: 0.5em;
    }

    &__MobileHp {
      display: none;
    }
  }

  .countdown .Shell__content {
    font-size: 1.5em;
  }
}

.Shell-content-mobile {
  display: none;
}

@media (max-width: 1000px) {
  .Shell {
    max-width: 100% !important;
    margin: 0 !important;

    .Shell__MobileHp {
      display: block !important;
    }

    &.Shell--hp {
      min-width: 42vw;
      bottom: 60vh;
      position: absolute;
      left: 10px;

      .Border {
        display: none !important;
      }

      &:last-of-type {
        left: auto;
        right: 10px;
        bottom: 65vh;
      }
    }

    .Shell__content {
      font-size: 12px !important;
      border-radius: 0.75em;
    }
  }

  .Shell__bar.Shell__bar--hp {
    display: none !important;

    .Border {
      display: none;
    }
  }

  .BottomShells {
    padding: 0.2em;
  }
}
