
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  $bottomShellsMarginX: 24px;

  font-family: "Space Mono", monospace!important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  * {
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    flex-direction: row;
    display: block;
    flex-shrink: unset;
  }

  img {
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    user-drag: none;
  }

  .Navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .Button {
      margin-left: 8px;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  .BottomShells {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 0 $bottomShellsMarginX;
    z-index: 2147483645;
    pointer-events: none;

    * {
      pointer-events: auto;
    }
  }

  .FloatingShells {
    display: flex;
    align-items: flex-end;
    /*position: fixed;*/
    top: 32px;
    right: $bottomShellsMarginX * 5;
  }

  .countdown {
    color: lightgreen;
    font-family: Arial, sans-serif!important;
    text-align: center;
    //position: fixed;
    //top: 40%;
    font-size: 1rem;
    //left: 50%;
    //transform: translateX(-50%);
    //text-shadow: 0px 0px 15px #ffffff;
    //background: rgba(255, 255, 255, 0.8);
    padding: 0.2em 1em;
    //border-radius: 50%;
  }

  .vdr-stick {
    background: transparent !important;
    border: none !important;
    box-shadow: none !important;
  }

  .vdr.active:before {
    content: none !important;
  }

  .fade-enter-active {
    transition: opacity 0.5s;
  }
  .fade-leave-active {
    transition: opacity 0.1s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }

  .fastfade-enter-active {
    transition: opacity 0.5s;
  }
  .fastfade-leave-active {
    transition: none;
  }
  .fastfade-enter, .fastfade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }

  // Dedalium on action buttons

  .dedalium-activated {
    -webkit-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 0 0 rgba(64, 190, 162, 0.8);
    box-shadow: 2px 5px 16px 0px #00795e, 0px 0px 0 0 rgba(64, 190, 162, 0.8);
    animation: dedalium-pulse 1s infinite;
    background: linear-gradient(3deg, #40bea2, 90%, white) !important;
    color: #ffffff !important;

    &:hover {
      transform: scale(1.1);
    }
  }

  .dedalium-activated--clicked {
    &:before {
      content: "+5";
      color: #00795e;
      font-size: 32px;
      position: absolute;
      top: -3rem;
      right: 0;
      -webkit-animation: dedaliumFadeOut 3s;
      animation: dedaliumFadeOut 3s;
      animation-fill-mode: forwards;
    }
  }

  .dedalium-icon-activated {
    fill: #00795e;
    filter: drop-shadow(0px 0px 4px #00795e);
    animation: dedaluim-icon-pulse 1s infinite;
  }

  .dedalium-icon-activated--clicked {
    &:before {
      content: "+5";
      color: #00795e;
      font-size: 32px;
      position: absolute;
      top: -1rem;
      right: 0;
      -webkit-animation: dedaliumFadeOut 3s;
      animation: dedaliumFadeOut 3s;
      animation-fill-mode: forwards;
    }
  }
}

@keyframes dedaliumFadeOut {
  0% {
    opacity: 1;
  }
  99% {
    opacity: 0.01;
    width: 100%;
    height: 100%;
  }
  100% {
    opacity: 0;
    width: 0;
    height: 0;
  }
}

@keyframes dedalium-icon-pulse {
  0% {
    filter: drop-shadow(0px 0px 0px #00795e);
  }
  15% {
    filter: drop-shadow(0px 0px 1px #00795e);
  }
  25% {
    filter: drop-shadow(0px 0px 2px #00795e);
  }
  35% {
    filter: drop-shadow(0px 0px 3px #00795e);
  }
  50% {
    filter: drop-shadow(0px 0px 4px #00795e);
  }
  65% {
    filter: drop-shadow(0px 0px 3px #00795e);
  }
  75% {
    filter: drop-shadow(0px 0px 2px #00795e);
  }
  85% {
    filter: drop-shadow(0px 0px 1px #00795e);
  }
  100% {
    filter: drop-shadow(0px 0px 0px #00795e);
  }
}

@keyframes dedalium-pulse {
  0% {
    -moz-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 0px rgba(64, 190, 162, 0.9);
    box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 0px rgba(64, 190, 162, 0.9);
    -webkit-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 0px rgba(64, 190, 162, 0.9);
  }
  15% {
    -moz-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 1px rgba(64, 190, 162, 0.9);
    box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 1px rgba(64, 190, 162, 0.9);
    -webkit-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 1px rgba(64, 190, 162, 0.9);
  }
  25% {
    -moz-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 2px rgba(64, 190, 162, 0.9);
    box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 2px rgba(64, 190, 162, 0.9);
    -webkit-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 2px rgba(64, 190, 162, 0.9);
  }
  35% {
    -moz-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 3px rgba(64, 190, 162, 0.9);
    box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 3px rgba(64, 190, 162, 0.9);
    -webkit-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 3px rgba(64, 190, 162, 0.9);
  }
  50% {
    -moz-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 20px 4px rgba(64, 190, 162, 0.9);
    box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 20px 4px rgba(64, 190, 162, 0.9);
    -webkit-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 20px 4px rgba(64, 190, 162, 0.9);
  }
  65% {
    -moz-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 3px rgba(64, 190, 162, 0.9);
    box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 3px rgba(64, 190, 162, 0.9);
    -webkit-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 3px rgba(64, 190, 162, 0.9);
  }
  75% {
    -moz-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 2px rgba(64, 190, 162, 0.9);
    box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 2px rgba(64, 190, 162, 0.9);
    -webkit-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 2px rgba(64, 190, 162, 0.9);
  }
  85% {
    -moz-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 1px rgba(64, 190, 162, 0.9);
    box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 1px rgba(64, 190, 162, 0.9);
    -webkit-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 1px rgba(64, 190, 162, 0.9);
  }
  100% {
    -moz-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 0px rgba(64, 190, 162, 0.9);
    box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 0px rgba(64, 190, 162, 0.9);
    -webkit-box-shadow: 2px 5px 16px 0px #00795e,
      0px 0px 10px 0px rgba(64, 190, 162, 0.9);
  }
}
