
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  .PlayerTurn {
    display: flex;
    flex-direction: column;
    //margin-top: -1em;
    max-width: 40vw;
    margin: -1em auto 0;

    &__name {
      color: #adecd8;
      text-transform: uppercase;
      text-shadow: 2px 2px 6px #adecd8;
      font-size: 16px;
      background: #1b1b24;
      padding: 2px 8px 8px;
      margin-top: 0.5em;
      border-radius: 0.75em;
      text-align: center;

      &.PlayerTurn__name--long {
        font-size: 14px;
        padding: 6px 8px 8px;
      }
    }
  }
}
