
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  img#dedalium-reward-icon__image {
    width: 100%!important;
    height: 100%!important;
    image-rendering: -webkit-optimize-contrast !important;
    filter: none !important;
    z-index: 2147483647 !important;
  }

  #dedalium-reward-icon__wrapper {
    margin: 0 auto !important;
    display: block !important;
    position: fixed !important;
    transform: translateY(-50%) translateX(-50%) !important;
    max-width: 120px !important;
    max-height: 120px !important;
    background: rgba(255, 255, 255, 0.1);
    border: 5px solid rgba(255, 255, 255, 0.1);
    cursor: pointer;
    z-index: 2147483647 !important;
    opacity: 0;

    &.dedalium-reward-icon__wrapper--opened {
      opacity: 0;
      animation-iteration-count: 1;
      animation-name: moveToBottom;
      animation-duration: 2.5s;
      animation-fill-mode: forwards;
      animation-delay: 1.3s;
      transform: translateX(-50%);
    }

    &.dedalium-reward-icon__wrapper--trash {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
      radial-gradient(50% 50% at 50% 50%, #dddddd 0%, #717171 100%);
      border: 5px solid #a6a6a6;
    }

    &.dedalium-reward-icon__wrapper--common {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
      radial-gradient(50% 50% at 50% 50%, #6cff84 0%, #277b35 100%);
      border: 5px solid #36bb4c;
    }

    &.dedalium-reward-icon__wrapper--rare {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
      radial-gradient(50% 50% at 50% 50%, #3cbdee 0%, #2c7793 100%);
      border: 5px solid #7ed0ef;
    }

    &.dedalium-reward-icon__wrapper--rareplus {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
      radial-gradient(50% 50% at 50% 50%, #2385f8 0%, #2b70c0 100%);
      border: 5px solid #2385f8;
    }

    &.dedalium-reward-icon__wrapper--epic {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
      radial-gradient(50% 50% at 50% 50%, #9304d9 0%, #420260 100%);
      border: 5px solid #9304d9;
    }

    &.dedalium-reward-icon__wrapper--legendary {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
      radial-gradient(50% 50% at 50% 50%, #ff8407 0%, #a65503 100%);
      border: 5px solid #ff8407;
    }
  }

  @keyframes moveToBottom {
    0% {
      opacity: 0.1;
      width: 10px;
      height: 10px;
    }
    50% {
      opacity: 0.7;
      left: calc(50% - 40px);
      top: 60%;
      width: 80px;
      height: 80px;
    }
    100% {
      opacity: 1;
      left: calc(50% + 30px);
      top: calc(100% - 280px);
      width: 120px;
      height: 120px;
    }
  }
}
