
.dedalium-speech-bubble {
  width: 500px;
  height: 300px;
  position: fixed;
  bottom: 45vh;
  right: 25vw;
  background: transparent;
  z-index: 99999999999;

  span {
    display: inline;
  }

  .Navigation {
    position: absolute;
    margin: 0 auto;
    left: 20%;
    bottom: 15%;
    width: 60%;
    font-size: .8rem;

    .Button {
      background: white;
      color: rgb(85, 88, 131);
      padding: 0 3rem;
      border-radius: 0;
    }
  }
}

.speech-bubble-image {
  max-width: 500px;
  width: 500px;
  height: 300px;
}

.speech-bubble-content {
  position: absolute;
  left: 20%;
  top: 12%;
  color: whitesmoke;
  max-width: 400px;
  height: 100%;
  max-height: 140px;
  font-size: 1.2rem;
  font-weight: 800;
  font-family: sans-serif!important;
}
