
#dedalium-quantity-container {
  width: 170px !important;
  height: 72px !important;
  right: 20px !important;
  top: -5px;
  position: fixed !important;
  z-index: 2147483647 !important;
  //background: linear-gradient(360deg, #58479a 0%, #010e1b 144.44%);
  background: linear-gradient(360deg, rgb(34, 20, 89) 0%, rgb(9, 92, 176) 144.44%);

  transform: skew(-20deg) !important;
  border: solid 3px !important;
  border-image-slice: 1 !important;
  border-image-source: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.8) 0%,
    rgba(255, 255, 255, 0) 100%
  ) !important;
  animation-name: dedaliumMoveDown !important;
  animation-duration: 4s !important;
  animation-fill-mode: forwards !important;

  .Navigation {
    animation: changeOpacity 2s;

    .Button {
      &:nth-child(1) {
        left: 2px
      }

      &:nth-child(2) {
        left: 54px
      }

      &:nth-child(3) {
        left: 112px
      }
    }
  }

  &.quantity-container-mission-finished {
    animation-name: dedaliumMoveDownAndStay !important;
    animation-fill-mode: forwards !important;

    img.dedalium-icon {
      margin-left: -10px;
    }

    &.quantity-container-dedalium {
      ::v-deep .Button {
        width: 70px !important;
      }
    }

    ::v-deep .Button {
      transform: translateX(-7%) skew(21deg) scale(0.8) !important;
      position: absolute;
      color: white;
      padding: 0;
      max-height: 50px;
      bottom: -55px;
      text-transform: uppercase;
      background: linear-gradient(
        360deg,
        #47529a 0%,
        rgb(34, 20, 89) 144.44%
      ) !important;
      width: 215px !important;
      border: solid 3px !important;
      border-image-slice: 1 !important;
      border-image-source: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.8) 0%,
        rgba(255, 255, 255, 0) 100%
      ) !important;
      border-radius: 16px !important;

      &:hover {
        background: linear-gradient(
          360deg,
          #010e1b 0%,
          #47529a 144.44%
        ) !important;
        opacity: 1;
      }

      .Button__inner {
        line-height: 32px;
      }
    }
  }

  div {
    text-align: center !important;
    height: 100% !important;
    transform: skew(0deg) !important;
  }

  span {
    text-align: center !important;
    vertical-align: middle !important;
    font-size: 36px !important;
    font-style: oblique !important;
    font-weight: 700 !important;
    letter-spacing: 0 !important;
    color: #f1f1f1 !important;
    font-family: sans-serif !important;
    transform: skew(30deg) !important;
    display: inline-block !important;
    margin: 5px 10px 0 0 !important;
    padding: 5px 10px !important;
    height: 100% !important;
    line-height: 50px !important;

    &.object-counter {
      font-size: 32px !important;

      &.object-counter--small {
        font-size: 24px !important;
      }

      img {
        height: 35px !important;
        margin: 0 -1rem 0 0.5rem !important;
      }
    }

    &.text-completed {
      padding: 0!important;
      margin: 0!important;
      width: 102%;

      img, span {
        display: block!important;
        margin: 0 auto!important;
      }

      img {
        max-height: 30px!important;
        position: relative;
        left: -10px;
      }

      span {
        font-size: 12px !important;
        transform: none!important;
        max-height: 65%;
        margin-top: -12px!important;
        padding-left: 25px!important;
      }
    }

    &.exp-hint {
      font-size: 10px!important;
      position: absolute!important;
      right: 0!important;
      top: -18px!important;
      padding: 0!important;
    }
  }

  img {
    vertical-align: top !important;
    transform: translateY(7px) skew(-12deg) !important;
    height: 40px !important;
    width: auto !important;
    padding: 0 5px 5px 5px !important;
    image-rendering: -webkit-optimize-contrast !important;
    display: inline-block !important;
    filter: none !important;

    &.store-logo {
      max-width: 40px;
      transform: none!important;
    }
  }
}

@keyframes dedaliumMoveDown {
  0% {
    top: -77px;
  }
  33% {
    top: -5px;
  }
  66% {
    top: -5px;
  }
  100% {
    top: -77px;
  }
}

@keyframes dedaliumMoveDownAndStay {
  0% {
    top: -77px;
  }
  33% {
    top: -5px;
  }
  66% {
    top: -5px;
  }
  100% {
    top: -5px;
  }
}

@keyframes changeOpacity {
  0% {
    opacity: 0;
  }

  70% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
