
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  .MissionDialogue {
    display: flex;
    flex-direction: column;
    transform: skew(2deg);

    img {
      max-height: 30vh;
      width: auto;
      max-width: 100%;
      margin: 0 auto;
    }

    &__title {
      color: #6a6b8e;
      font-size: 20px;
      border-radius: 0.75rem;
      text-align: center;
      padding: 0.5rem;
    }

    &__winner {
      margin-top: 0.2rem;
      color: #adecd8;
      text-transform: uppercase;
      text-shadow: 2px 2px 6px #adecd8;
      font-size: 16px;
      background: #1b1b24;
      padding: 0.5rem;
      border-radius: 0.75rem;
      text-align: center;
    }

    &__bottom {
      display: flex;
      flex-direction: column;
      align-items: center;
      color: #6a6b8e;
      margin-top: 32px;

      .Navigation {
        width: 90%;
        justify-content: space-around;

        .Button {
          &:first-of-type {
            border-color: gray;
          }

          padding: 10px 25px;
        }
      }
    }

    &__stats {
      display: flex;
      align-items: center;
      color: #5c5e85;
      font-weight: bold;
      font-size: 1.1rem;
      background: #1b1b24;
      padding: 0.5rem;
      border-radius: 0.75rem;
      flex: 1;
      margin-right: 1.5rem;

      > div {
        /*margin-left: 2rem;*/
        margin: 0 0.5rem;
      }
    }

    &__message {
      text-align: center;
      color: white;
      font-family: sans-serif!important;
      font-size: 15px;
      font-weight: 500;
      padding: 0.5rem;
      line-height: 22px;
      border-radius: 0.75rem;
      -webkit-font-smoothing: antialiased;

      span {
        display: inline;
        font-weight: 900;
      }
    }
  }
}

@media (max-width: 1000px) {
  #zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
    .MissionDialogue {
      top: 5% !important;
      
      img {
        max-height: 15vh;
      }
    }
  }
}
