#dedaliumCanvas {
  display: block !important;
  width: 100% !important;
  height: 100% !important;
  left: 0 !important;
  top: 0 !important;
  position: fixed !important;
  outline: none !important;
  z-index: 2147483646 !important;
  transition: opacity 1s !important;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
  //pointer-events: none;
}

#DedaliumAppContainer {
  bottom: 0;
  width: 100%;
  min-height: 100vh;
  position: fixed !important;
  z-index: 2147483646 !important;
  pointer-events: none;
  cursor: pointer;
}

#MasterAppContainer {
  bottom: 0;
  width: 100%;
  min-height: 100vh;
  position: fixed !important;
  z-index: 2147483646 !important;
  pointer-events: none;
  cursor: pointer;
}

#principalContainer {
  position: relative !important;
  z-index: 2147483646 !important;
  opacity: 0;
  transition: opacity 1s !important;
  pointer-events: none;
}

#dedalium-clickDiv {
  position: fixed !important;
  z-index: 2147483645 !important;
  cursor: pointer !important;
  background: transparent !important;
}

#dedalium-npc-clickDiv, #dedalium-box-clickDiv {
  position: fixed !important;
  z-index: 2147483646 !important;
  background: transparent !important;
}

#dedalium-box-clickDiv {
  cursor: pointer !important;
}

