#dedalium-game-control-wrapper {
  display: none;
}

#dedalium-quantity-container {
  width: 170px !important;
  height: 72px !important;
  right: 20px !important;
  top: -5px;
  position: fixed !important;
  z-index: 2147483647 !important;
  //background: linear-gradient(
  //                360deg
  //        , #2E5271 0%, #182738 144.44%);
  //background: linear-gradient(
  //                360deg
  //        , rgb(89 105 191) 0%, rgb(24, 39, 56) 144.44%);

  background: linear-gradient(360deg, #58479a 0%, #010e1b 144.44%);

  transform: skew(-20deg) !important;
  border: solid 3px !important;
  border-image-slice: 1 !important;
  border-image-source: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.8) 0%,
    rgba(255, 255, 255, 0) 100%
  ) !important;
  animation-name: dedaliumMoveDown !important;
  animation-duration: 4s !important;

  div {
    text-align: center !important;
    height: 100% !important;
    transform: skew(0deg) !important;
  }

  span {
    text-align: center !important;
    vertical-align: middle !important;
    font-size: 36px !important;
    font-style: oblique !important;
    font-weight: 700 !important;
    letter-spacing: 0 !important;
    color: #f1f1f1 !important;
    font-family: sans-serif !important;
    transform: skew(30deg) !important;
    display: inline-block !important;
    margin: 5px 10px 0 0 !important;
    padding: 5px 10px !important;
    height: 100% !important;
    line-height: 50px !important;

    &.object-counter {
      font-size: 32px!important;

      img {
        height: 35px!important;
        margin: 0 -1rem 0 0.5rem!important;
      }
    }
  }

  img {
    vertical-align: top !important;
    transform: translateY(7px) skew(-12deg) !important;
    height: 40px !important;
    width: auto !important;
    padding: 0 5px 5px 5px !important;
    image-rendering: -webkit-optimize-contrast !important;
    display: inline-block !important;
    filter: none !important;
  }
}

@keyframes dedaliumMoveDown {
  0% {
    top: -77px;
  }
  33% {
    top: -5px;
  }
  66% {
    top: -5px;
  }
  100% {
    top: -77px;
  }
}

@keyframes dedaliumFadein {
  0% {
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  66% {
    opacity: 1;
  }
  95% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes blink-social {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(10deg);
  }
  50% {
    transform: rotate(0deg);
    box-shadow: 0 0 0 rgb(64, 190, 162), 0 0 3px rgb(64, 190, 162), 0 0 5px rgb(64, 190, 162),
    0 0 10px rgb(64, 190, 162), 0 0 30px rgb(64, 190, 162), 0 0 50px rgb(64, 190, 162);
  }
  75% {
    transform: rotate(-10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

#dedalium-social-interaction-reward {
  font-family: Impact, sans-serif;
  display: flex;
  align-items: center;
  padding: 0.25rem 1rem;
  position: absolute;
  bottom: 0;
  right: 0;
  background: none;
  color: lightgreen;
  font-weight: 800;
  font-size: 1.8em;
  z-index: 9999;
  border-radius: 5px;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #000;
  text-shadow: 1px 1px 0 black, 2px 2px 0 black;
  letter-spacing: 1px;
  animation: rewardMoveUp 2s ease-in-out forwards;
}

@keyframes rewardMoveUp {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  50% {
    transform: translateY(-40px);
    opacity: 0.7;
  }
  75% {
    transform: translateY(-60px);
    opacity: 0.4;
  }
  100% {
    transform: translateY(-80px);
    opacity: 0;
  }
}

