.bounce-plain-enter-active {
  z-index: 99;
  transform-origin: center;
  animation: bounce-in .7s;
}
.bounce-plain-leave-active {
  z-index: -1;
  transform-origin: center;
  animation: bounce-out .7s;
}

@keyframes bounce-plain-in {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes bounce-out {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
    opacity: 0;
  }
}


@keyframes bounce-mid-in {
  0% {
    transform: translateX(-50%) scale(0);
  }
  100% {
    transform: translateX(-50%) scale(1);
  }
}

.battle-bounce-mid-enter-active {
  z-index: 99;
  animation: bounce-mid-in .6s;
  transform-origin: center;
}
.battle-bounce-mid-leave-active {
  z-index: -1;
  opacity: 0;
}

.pinned-hint {
  position: fixed;
  top: 0;
  right: 0;
  max-width: 600px;
  height: 400px;
  padding: 1rem 2rem 2rem 2rem;
  border: 5px solid #484873;
  background: #333453;

  .pinned-hint__close {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 25px;
    height: 25px;
    cursor: pointer;
  }
  
  p {
    margin: 0.5rem auto 1rem;
  }

  img {
    max-height: 85%;
  }

  h2, p {
    color: white;
    text-align: center;
  }
}

.earthquake-ded {
  animation: shake 0.5s;
  animation-iteration-count: 1;
}

.fall-ded {
  animation-iteration-count: 1;

  &.fall-ded-left {
    animation: fall-left 2s;
  }

  &.fall-ded-right {
    animation: fall-right 2s;
  }
}

.explode-ded {
  transition: 0.5s;
  opacity: 0;
  transform: scale(3) rotate(10deg);
}

@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes fall-left{
  0%{
    transform: translateY(0) rotate(25deg);
    opacity: 1;
  }
  100%{
    transform: translateY(20em) rotate(25deg);
    opacity: 0;
  }
}

@keyframes fall-right{
  0%{
    transform: translateY(0) rotate(-25deg);
    opacity: 1;
  }
  100%{
    transform: translateY(20em) rotate(-25deg);
    opacity: 0;
  }
}

