
#zd5cc103-5d5b-4c3e-aec7-a15805dba881 {
  .Button {
    background: linear-gradient(rgb(85, 88, 131), #373955);
    border: 2px solid #60e2da;
    color: #60e2da;
    border-radius: 2em;
    padding: 0.3em 0.6em;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.2s ease-in;
    text-align: center;
    line-height: 1;
    height: auto!important;

    &:hover {
      opacity: 0.85;
      background: #373955;
      transform: scale(1.03);
    }

    &--spaced {
      margin: 0 0.2em;
    }

    &--is-circle {
      border-radius: 50%;

      .Button__inner {
        border-radius: 50%;
        padding: 0.3em;
      }
    }

    &--nowrap {
      white-space: nowrap;
    }

    &__inner {
      padding: 5px 7px;
      border-radius: 32px;
      font-size: 16px;

      a, span {
        color: white;
        font-size: 16px;
        text-decoration: none;
      }

      img {
        max-width: 25px;
        display: inline-block;
        vertical-align: middle;
        filter: drop-shadow(0px 0px 3px #09FAC1);
        margin-right: 5px;
      }
    }
  }
}
